body {
  background-image: url("./data/wasps.png");
  background-repeat: no-repeat;
  background-size: cover;
 }

.site {
  display: flex;
  flex-direction: column;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.site-nav {
  background-color: rgba(255,255,255,0.6);
  border-radius: 5px;
}

.site-nav-links {
  display: flex;
  flex-direction: row;

  justify-content: space-evenly;

  list-style-type: none;
}

.releases {
  display: flex;
  flex-direction: column;
  margin: 20px;

  background: rgba(255,255,255, 0.6);
  border-radius: 20px;
}

.release-title {
  font-size: x-large;
  text-align: center;
  padding: 10px;
}

.release-tracks {
  display: flex;
  justify-content: space-around;
}

.home {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.new-music {
  display: flex;
  flex-direction: column;
}

.label-logo {
  height: auto;
  width: auto;
  max-width: 300px;
  max-height: 300px;
  align-self: center;
  padding-bottom: 20px;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

.check-back-soon {
  font-size: large;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  padding: 20px;

  background: rgba(255,255,255, 0.6);
  border-radius: 20px;
}

.site-footer {
  display: flex;
  flex-direction: row-reverse;
  align-self: center;

  position: fixed;
  left: 50%;
  top: 95%;
  transform: translate(-50%, -95%);
}

.merch-link {
  font-size: large;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  padding: 20px;

  background: rgba(255,255,255, 0.6);
  border-radius: 20px;
}

.site-footer-logo {
  height: auto;
  width: auto;
  max-width: 100px;
  max-height: 100px;
}

.tracks-section .track-wrapper {
  padding-bottom: 20px;
}

.track-info {
  padding-bottom: 10px;
}